.project-container {
  background-color: #fff;
  width: 80%;
  border-radius: 5px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 0 auto;
  /* margin: 20px 0; */
}

.project-name {
  margin-top: 0;
}

.project-description {
  font-size: 1.2rem;
  line-height: 1.5;
  margin: 10px 0;
}

.project-tech-stack {
  font-weight: bold;
  margin-bottom: 10px;
}

.project-links {
  display: flex;
  margin-top: 20px;
}

.project-link {
  background-color: #333;
  border-radius: 5px;
  margin-right: 3rem;
  color: #fff;
  display: inline-block;
  padding: 10px 15px;
  text-decoration: none;
  transition: all 0.3s ease;
}

.project-link:hover {
  background-color: #555;
}

.project-link i {
  margin-right: 5px;
}

.project-details {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 20px;
}

.project-demo .project-gif {
  width: 100%;
  background-color: #ffffff;
  object-fit: fill;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  margin: 1rem 0;
}

.project-feedback {
  display: inline;
  width: 70%;
  margin-left: 2rem;
}

.project-feedback label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.project-feedback input,
.project-feedback textarea {
  padding: 0.5rem;
  margin-bottom: 1rem;
  border-radius: 0.25rem;
  border: none;
  display: block;
  width: 100%;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.project-feedback input:focus,
.project-feedback textarea:focus {
  outline: none;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
}

.project-feedback textarea {
  resize: none;
  height: 100px;
}

.project-feedback button {
  padding: 0.5rem 1rem;
  border: none;
  display: block;
  border-radius: 0.25rem;
  background-color: #0072C6;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.project-feedback button:hover {
  background-color: #004D8C;
}

